import React from 'react';

import { Card, CardContent, makeStyles } from '@material-ui/core';
import clsx from 'clsx';

import { CommonComponentProps } from '../../components';

const useStyles = makeStyles((theme) => ({
  root: {
    background: 'var(--app-bg-color)',
  },
  colorItemContainer: {
    display: 'grid',
    gridTemplateColumns: 'repeat(3, auto)',
    gridGap: '1em',
    padding: theme.spacing(1),
    '&:last-child': {
      paddingBottom: theme.spacing(1),
    },
  },
}));

export type SettingsPickerContainerProps = Partial<
  React.PropsWithChildren<CommonComponentProps>
> & {
  cardContentStyle?: React.CSSProperties;
};

export const SettingsPickerContainer: React.FC<SettingsPickerContainerProps> = (
  props: SettingsPickerContainerProps
) => {
  const classes = useStyles();
  return (
    <Card elevation={0} className={clsx(classes.root, props.className)}>
      <CardContent
        className={clsx(classes.colorItemContainer)}
        style={props.cardContentStyle}>
        {props.children}
      </CardContent>
    </Card>
  );
};
