import React from 'react';

import { ThemeProvider } from '@material-ui/core';
import log from 'loglevel';
import ReactDOM from 'react-dom';
import { ReactReduxFirebaseProvider } from 'react-redux-firebase';

import './index.scss';

import App from './App';
import { REDUX_FIREBASE_CONF } from './firebase';
import reportWebVitals from './reportWebVitals';
import * as serviceWorkerRegistration from './serviceWorkerRegistration';
import { StoreProvider } from './state';
import AppTheme from './views/styles/theme';

const { NODE_ENV } = process.env;
// disables logging in production release
log.setLevel(NODE_ENV === 'production' ? 'SILENT' : 'trace');

ReactDOM.render(
  <StoreProvider>
    <ReactReduxFirebaseProvider {...REDUX_FIREBASE_CONF}>
      <ThemeProvider theme={AppTheme}>
        <App />
      </ThemeProvider>
    </ReactReduxFirebaseProvider>
  </StoreProvider>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://cra.link/PWA
serviceWorkerRegistration.register();

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
